@import url(https://fonts.googleapis.com/css?family=PT+Sans:400,700,400italic,700italic);
@import url("https://fonts.googleapis.com/css?family=Tenor+Sans");
@import url("https://fonts.googleapis.com/css?family=Archivo+Black");
@import '_variables';

#pricing {

  * {
    padding: 0;
    box-sizing: border-box;
    -moz-box-sizing: border-box;
  }

  html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, figcaption, figure, footer, header, hgroup, menu, nav, section, summary, time, mark, audio, video {
    padding: 0;
    border: 0;
    outline: 0;
    vertical-align: top;
    box-sizing: border-box;
    -moz-box-sizing: border-box;
  }

  body {
    font-family: "Source Sans Pro", "helvetica", sans-serif;
    font-style: normal;
    font-weight: normal;
    text-align: center;
    font-size: 16px;
    color: rgba(39, 65, 90, 1);
  }

  h4 {
    margin-bottom: 12px;
    font-size: 1.25em;
    font-weight: 400;
    text-transform: uppercase;
  }

  h5 {
    font-size: 1.75em;
  }

  p {
    margin-top: 1em;
    margin-bottom: 1em;
    color: rgba(39, 65, 90, .9);
    font-size: 1.25em;
    line-height: 1.625em;
  }

  ol, ul {
    list-style: none;
  }

  body {
    line-height: 1;
  }

  strong {
    color: rgba(39, 65, 90, 1);
    font-weight: 600;
  }

  .layer {
    clear: both;
    width: 100%;
    height: auto;
    padding: 60px 7.5% 60px;
    display: block;
  }

  .layer > section, .layer > article {
    clear: both;
    width: 100%;
    height: auto;
    max-width: 1092px;
    margin: 0 auto;
    display: block;
  }

  .third {
    width: 27.95%;
    margin: 0 30px 0 0;
    display: inline-block;
  }

  .lift {
    position: relative;
    -webkit-transition: all .075s ease-out;
    -moz-transition: all .075s ease-out;
    -o-transition: all .075s ease-out;
    transition: all .075s ease-out;
  }

  .lift:hover {
    top: -3px;
    -webkit-box-shadow: 0 2px 6px rgba(39, 65, 90, .15);
    -moz-box-shadow: 0 2px 6px rgba(39, 65, 90, .15);
    box-shadow: 0 2px 6px rgba(39, 65, 90, .15);
  }

  .plan-tier {
    cursor: pointer;
    border: 1px solid $black;
    background: white;
    vertical-align: baseline;
    border-radius: 3px;
    -moz-border-radius: 3px;
    cursor: pointer;
    overflow: hidden;
    margin-top: 50px;
  }

  .plan-tier h4 {
    padding: 18px 0 15px;
    margin: 0 0 30px;
    background: $black;
    color: white;
  }

  .plan-tier sup {
    position: relative;
    right: -9px;
  }

  .plan-tier ul {
    margin: 30px 0 0;
    border-top: 2px solid #e7f2f0;
  }

  .plan-tier ul li {
    font-size: 1.25em;
    padding: 18px 0;
    color: rgba(39, 65, 90, .9);
    border-bottom: 2px solid #e7f2f0;
  }

  sup {
    vertical-align: top;
  }

  .plan-tier .plan-price {
    font-size: 2em;
    font-weight: 300;
    letter-spacing: -3px;
  }

  .plan-tier sub {
    vertical-align: bottom;
    position: relative;
    bottom: .875em;
  }

  .plan-tier .early-adopter-price {
    color: #0091e5;
  }
  p:first-of-type {
    margin-top: 0;
  }

  small, del {
    color: rgba(39, 65, 90, .5);
    font-size: 1em;
    line-height: 1.5em;
  }

  s, strike, del {
    text-decoration: line-through;
  }

  .layer {
    padding-top: 0;
    padding-bottom: 0;
  }

  .plan-tier {
    &.callout {
      border-color: $dark-yellow;
      margin-top: 0;
      h4 {
        background-color: $dark-yellow;
      }

      h6 {
        width: 100%;
        padding: 15px 0 14px;
        background: $dark-yellow;
        color: $yellow-light;
        font-size: 1.25em;
        margin: 0;
        border-bottom: solid 1px $yellow-light;
      }
    }
  }


  @media (min-width: 768px) {

    .col-sm-4 {
      width: 27.2727%
    }

    .col-offset-right {
      margin-right: 9.0909%;
    }

    .col-offset-left {
      margin-left: 9.0909%;
    }
  }

  sup {
    top: .5em;
    left: -.5em;
  }

  .btn {
    display: inline-block;
    padding: 6px 12px;
    margin-bottom: 10px;
    font-size: 14px;
    font-weight: 400;
    line-height: 1.42857143;
    text-align: center;
    white-space: nowrap;
    vertical-align: middle;
    -ms-touch-action: manipulation;
    touch-action: manipulation;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    background-image: none;
    border: 1px solid transparent;
    border-radius: 4px;
  }

  .fine-print {
    padding: 1em;
  }

  section.plans {
    display: flex;

    .plan-tier {
      flex: 1;
    }
  }

  .matchHeight {
    height: 446px;
  }

  .margin67 {
    margin-bottom: 82px;
  }

  padding: 0px 0 50px 0;
  margin-top: -125px;

}

@media (min-width: 768px) {
  .navbar-right {
    margin-right: 0;
  }
}

#pricing-intro {
  padding: 150px 0 100px 0;
  background-color: #222;
  color: #FFF;
}

.accordion {
  max-width: 600px;
  margin: 0 auto;
}

.accordion-toggle {
  border-bottom: 1px solid #cccccc;
  cursor: pointer;
  margin: 0;
  padding: 10px 0;
  position: relative;
}

.accordion-toggle.active:after {
  content: "";
  position: absolute;
  right: 0;
  top: 17px;
  width: 0;
  height: 0;
  border-bottom: 5px solid $dark-yellow;
  border-left: 5px solid rgba(0, 0, 0, 0);
  border-right: 5px solid rgba(0, 0, 0, 0);
}

.accordion-toggle:before {
  content: "";
  position: absolute;
  right: 0;
  top: 17px;
  width: 0;
  height: 0;
  border-top: 5px solid $black;
  border-left: 5px solid rgba(0, 0, 0, 0);
  border-right: 5px solid rgba(0, 0, 0, 0);
}

.accordion-toggle.active:before {
  display: none;
}

.accordion-content {
  display: none;
}

.accordion-toggle.active {
  color: $dark-yellow;
}
